@mixin price-block {
  margin-left: 7%;
    margin-bottom: 10px;
    @media(min-width: 600px){
      margin-bottom: 20px;
    }

    @media(min-width: 768px){
      min-width: 160px;
      margin-bottom: 0;
    }
    &__discount{
      color: $price-discount;
      font-size: 16px;
      line-height: 110.4%;
      text-decoration-line: line-through;
      margin-bottom: 0;

    }
    &__price{
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: $black;
      margin-bottom: 0;
      @media(min-width: 768px){
        font-size: 25px;
        line-height: 30px;
      }
    }
}

@mixin tick-bullet($inverted: false) {
  position: relative;
  padding-left: 34px;
  &::before{
    display: block;
    position: absolute;
    left: 0px;
    top: 6px;
    content: '';
    width:19.09px;
    height: 18px;
    background: transparent;
    @if $inverted{
      background-image: url('../../images/icons/empty-tick-white.svg') !important;
    } @else {
      background-image: url('../../images/icons/empty-tick-black.svg') !important;
    }
    background-size: 19.09px 18px;
    background-repeat: no-repeat;
  }

    @media(min-width: 768px){
      padding-left: 39px;  
      position: relative;      
      &::before{
        display: block;
        position: absolute;
        left: 6px;
        top: 0px;
        content: '';
        width:24.09px;
        height: 23px;
        background: transparent;
        background-size: 24.09px 23px;
        background-repeat: no-repeat;
    }
  }

  @media(min-width: 1024px){
    padding-left: 44px;
    &::before{
      display: block;
      position: absolute;
      left: 0px;
      top: -3px;
      content: '';
      width:29.09px;
      height: 28px;
      background: transparent;
      background-size: 29.09px 28px;
      background-repeat: no-repeat;
    }
  }
}
